___CSS_LOADER_EXPORT___.push([module.id, `/* Split pane styling */
.gutter {
  background-color: #eee;
  background-repeat: no-repeat;
  background-position: 50%;
  flex-shrink: 0; /* Prevent gutter from shrinking */
}

.dark .gutter {
  background-color: #2a2a2a;
}

.gutter:hover {
  background-color: #ddd;
}

.dark .gutter:hover {
  background-color: #3a3a3a;
}

.gutter.gutter-horizontal {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  cursor: col-resize;
  width: 5px !important; /* Enforce consistent width */
}

.gutter.gutter-vertical {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  cursor: row-resize;
  height: 5px !important; /* Enforce consistent height */
}

/* Make sure the split container takes full height */
.split {
  display: flex;
  height: 100%;
  min-height: 0; /* Changed from 100% to 0 to prevent overflow */
  width: 100%;
  overflow: hidden; /* Prevent overflow issues */
}

.split.vertical {
  flex-direction: column;
}

/* Remove float which can cause layout issues */
.split, .gutter.gutter-horizontal {
  float: none; /* Changed from left to none */
}

/* Ensure child containers behave properly */
.split > div {
  height: 100%;
  min-height: 0; /* Allow shrinking below content size */
  min-width: 0; /* Allow shrinking below content size */
  display: flex;
  flex-direction: column;
  overflow: hidden; /* Prevent content from overflowing */
}

/* Ensure the preview container fills its parent height */
.preview-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0; /* Changed from 100% to 0 to prevent overflow */
  overflow: hidden; /* Prevent content from overflowing */
  position: relative; /* For proper child positioning */
}

/* Force editor and preview to use full available height */
.editor, .markdown-preview {
  flex: 1 1 auto;
  min-height: 0; /* Allow shrinking */
  min-width: 0; /* Allow shrinking */
  height: auto; /* Auto height based on container */
  width: 100%;
  overflow: auto; /* Allow scrolling inside components */
  position: relative;
}

/* Enhanced scrollbar styling specifically for editor and preview */
.editor::-webkit-scrollbar,
.markdown-preview::-webkit-scrollbar,
.cm-scroller::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.editor::-webkit-scrollbar-thumb,
.markdown-preview::-webkit-scrollbar-thumb,
.cm-scroller::-webkit-scrollbar-thumb {
  background-color: rgba(156, 163, 175, 0.3);
  border-radius: 3px;
}

.editor::-webkit-scrollbar-thumb:hover,
.markdown-preview::-webkit-scrollbar-thumb:hover,
.cm-scroller::-webkit-scrollbar-thumb:hover {
  background-color: rgba(156, 163, 175, 0.5);
}

.dark .editor::-webkit-scrollbar-thumb,
.dark .markdown-preview::-webkit-scrollbar-thumb,
.dark .cm-scroller::-webkit-scrollbar-thumb {
  background-color: rgba(75, 85, 99, 0.3);
}

.dark .editor::-webkit-scrollbar-thumb:hover,
.dark .markdown-preview::-webkit-scrollbar-thumb:hover,
.dark .cm-scroller::-webkit-scrollbar-thumb:hover {
  background-color: rgba(75, 85, 99, 0.5);
}

/* Add specific styling for mobile view */
@media (max-width: 768px) {
  .split.vertical > div {
    min-height: 150px; /* Minimum height for panels in mobile view */
  }
  
  .editor, .markdown-preview {
    min-height: 150px;
  }
} `, "",{"version":3,"sources":["webpack://./src/splitPane.css"],"names":[],"mappings":"AAAA,uBAAuB;AACvB;EACE,sBAAsB;EACtB,4BAA4B;EAC5B,wBAAwB;EACxB,cAAc,EAAE,kCAAkC;AACpD;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yDAA2K;EAC3K,kBAAkB;EAClB,qBAAqB,EAAE,6BAA6B;AACtD;;AAEA;EACE,yDAA2L;EAC3L,kBAAkB;EAClB,sBAAsB,EAAE,8BAA8B;AACxD;;AAEA,oDAAoD;AACpD;EACE,aAAa;EACb,YAAY;EACZ,aAAa,EAAE,+CAA+C;EAC9D,WAAW;EACX,gBAAgB,EAAE,4BAA4B;AAChD;;AAEA;EACE,sBAAsB;AACxB;;AAEA,+CAA+C;AAC/C;EACE,WAAW,EAAE,8BAA8B;AAC7C;;AAEA,4CAA4C;AAC5C;EACE,YAAY;EACZ,aAAa,EAAE,uCAAuC;EACtD,YAAY,EAAE,uCAAuC;EACrD,aAAa;EACb,sBAAsB;EACtB,gBAAgB,EAAE,qCAAqC;AACzD;;AAEA,yDAAyD;AACzD;EACE,OAAO;EACP,aAAa;EACb,sBAAsB;EACtB,aAAa,EAAE,+CAA+C;EAC9D,gBAAgB,EAAE,qCAAqC;EACvD,kBAAkB,EAAE,iCAAiC;AACvD;;AAEA,0DAA0D;AAC1D;EACE,cAAc;EACd,aAAa,EAAE,oBAAoB;EACnC,YAAY,EAAE,oBAAoB;EAClC,YAAY,EAAE,mCAAmC;EACjD,WAAW;EACX,cAAc,EAAE,sCAAsC;EACtD,kBAAkB;AACpB;;AAEA,mEAAmE;AACnE;;;EAGE,UAAU;EACV,WAAW;AACb;;AAEA;;;EAGE,0CAA0C;EAC1C,kBAAkB;AACpB;;AAEA;;;EAGE,0CAA0C;AAC5C;;AAEA;;;EAGE,uCAAuC;AACzC;;AAEA;;;EAGE,uCAAuC;AACzC;;AAEA,yCAAyC;AACzC;EACE;IACE,iBAAiB,EAAE,6CAA6C;EAClE;;EAEA;IACE,iBAAiB;EACnB;AACF","sourcesContent":["/* Split pane styling */\r\n.gutter {\r\n  background-color: #eee;\r\n  background-repeat: no-repeat;\r\n  background-position: 50%;\r\n  flex-shrink: 0; /* Prevent gutter from shrinking */\r\n}\r\n\r\n.dark .gutter {\r\n  background-color: #2a2a2a;\r\n}\r\n\r\n.gutter:hover {\r\n  background-color: #ddd;\r\n}\r\n\r\n.dark .gutter:hover {\r\n  background-color: #3a3a3a;\r\n}\r\n\r\n.gutter.gutter-horizontal {\r\n  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAIklEQVQoU2M4c+bMfxAGAgYYmwGrIIiDjrELjpo5aiZeMwF+yNnOs5KSvgAAAABJRU5ErkJggg==');\r\n  cursor: col-resize;\r\n  width: 5px !important; /* Enforce consistent width */\r\n}\r\n\r\n.gutter.gutter-vertical {\r\n  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUg